	module.exports = {"data":{"markdownRemark":{"html":"<p>「中国の発展は世界から切り離すことができない」 - 「鄧小平による選ばれた作品第3巻における国際政治の戦略的考察」を書く第3巻では、鄧小平は国際政治と国際関係について集中的に議論した。鄧小平はより実践的な国際戦略家であると言わざるを得ないが、毛沢東は詩人の気質の強い比較的哲学的な国際戦略家であり、多くの問題を抱えており、非常に落胆しており、強いイデオロギー的色彩を持っている。鄧小平氏は、中国の発展に有益な戦略的選択についてより懸念している。基本的な変化は、イデオロギーの観点から国際的にスローガンではなく、国家経済の発展に集中できるように戦争を避けることである。国際戦略の状況と決定は国益の原則に基づくべきであると同時に、中国が発展したい場合には、国際社会にあらゆる面で参加しなければならない。</p>\n<p>彼は国際的な問題を解決するための新しい方法やアイデアを発見することにもっと注意を払います。香港の問題は発明と言えるでしょうし、他の問題についても知りたいと思っています。彼は国家主権を守るために「国家権力」という概念を提案したが、西側諸国は現在主権を超越するという概念を持っているが、途上国にとっては依然として重要であり、これを上回ることはできない。 「これは「人権」にとって非常に創造的な概念です。</p>\n<p>毛沢東は世界の影響を重視しているので、多くの驚くべき動きがあります。鄧小平（Deng Xiaoping）もこのような考えを持っていた。彼は、中国のような大きな国が世界と人類に大きな貢献をしなければならないという事実に大きな注意を払った。</p>\n<p>これについて繰り返し話してください。前の期間、最も有名な博覧会は、彼が静かに観察し、落ち着いて対処し、自分の立場を安定させ、懸命に働いているということでした。</p>\n<p>中国の現在の強さは国際的な分野で大きな役割を果たすことができないので、低プロファイルが必要です。将来的には中国が発展し、中国の利益は国際化され、より優位な国際的地位を獲得しなければならない。古典的なエロティックな文化について、オランダ人が書いた「The Secret Screening Test」、非常に強力ではあるが、それほど良くない「女性のセックス」、教育された若者の「Tropic」、Miller's、それは禁止された本でした。今日では、このような書籍がたくさんあり、すべてが「経済的利益」です。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0007.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0007.md","frontmatter":{"title":"1月10日 月曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0007.md absPath of file >>> MarkdownRemark"}}

